import React, { Component, PropTypes } from 'react';
import { Router, Route, Link ,hashHistory} from 'react-router';
// start
import './dropDownMenu.scss'
// end

import { Popup, List, Button, InputItem } from 'antd-mobile';


class PopupContent extends Component {

    constructor(){
        super();
    }
    componentWillMount(){
        this.state = {
            sel: '',
        }
    }
    onSel(sel){
        this.setState({ sel });
        this.props.onClose();
    }
    render() {
        return (
            <List renderHeader={() => `${this.state.sel}`}>
                <List.Item
                    thumb="https://zos.alipayobjects.com/rmsportal/dNuvNrtqUztHCwM.png"
                    onClick={() => { this.onSel(''); }}
                ></List.Item>
                <List.Item
                    thumb="https://zos.alipayobjects.com/rmsportal/UmbJMbWOejVOpxe.png"
                    onClick={() => { this.onSel(''); }}
                ></List.Item>
                <InputItem value={this.state.val} onChange={val => this.setState({ val })}></InputItem>
            </List>
        );
    }
}

const Test = () => {
    const onMaskClose = () => {
        console.log('onMaskClose');
        // also support Promise
        // return new Promise((resolve) => {
        //   console.log('1000ms ');
        //   setTimeout(resolve, 1000);
        // });
    };
    const onClick = (e) => {
        e.preventDefault(); //  Android 
        Popup.show(<PopupContent onClose={() => Popup.hide()} />, { onMaskClose });
    };
    // newInstance() {
    //  const ins = Popup.newInstance();
    //  ins.show(<Button onClick={() => ins.hide()}></Button>);
    // },
    return (
        <div style={{ padding: '0.15rem' }}>
            <Button onClick={onClick}></Button>
        </div>
    );
};

class DropDownMenu extends Component{
    constructor(){
        super();
    }
    componentWillMount(){

        //
        this.state={
            link:"1"
        }
    }

    componentDidMount(){
        //  start
        componentStore.set(this);
        //  end
    };
    componentWillUnmount(){
        //  start
        componentStore.clear(this);
        //  end
    };



    render(){

        return (
            <div className="dropDownMenu">
                <Test />
            </div>
        )
    }

}

export default DropDownMenu;




